.photo {
  width: 150px;
  height: 150px;
}
.git {
  margin: 0 0 0 90px;
}
.mail {
  margin: 0 0 0 30px;
}
.git,.mail {
  width: 40px;
  height: 40px;
  display: inline-block;
  -webkit-transition: 0.5s linear;
  -webkit-transition: 0.5s ease-in;
  transition: 0.5s ease-in;
}
.git:hover {
  -webkit-transform: rotate(-360deg);
  transform: rotate(-360deg);
  opacity: 0.5;
}
.mail:hover {
  -webkit-transform: rotate(-360deg);
  transform: rotate(-360deg);
  opacity: 0.5;
}
